import React, { Component } from 'react'
import { Container } from '@material-ui/core'
import axios from 'axios'
// import logo from './logo.svg'
import './App.css'

import Stepper from './components/Stepper'

class App extends Component {
  state = {
    image: null,
    cropped: null,
    enhanced: null,
    requested: false,
    // // 4 testing
    // image: "data:image/jpeg;base64,",
  }

  setImage = (image) => {
    this.setState({image: image})
  }

  setCropped = (image) => {
    this.setState({
      cropped: image,
      enhanced: null,
      requested: false,
      // // 4 testing
      // enhanced: image
    })
  }

  // Might be better to take image as input
  enhance = () => {
    this.setState({requested: true})
    axios.post("http://127.0.0.1:4158/enhance", {image: this.state.cropped})
      .then(response => {
        if (response.data.error !== undefined) {
          window.alert(
            `Сервер вернул "${response.data.error}", свяжитесь с автором`
          )
          this.setState({requested: false})
        }
        this.setState({enhanced: response.data.image})
      })
      .catch(error => {
        window.alert("Сервер offline")
        this.setState({requested: false})
      })
  }

  resetState = () => {
    // TODO: refactor to use single initial state
    this.setState({
      image: null,
      cropped: null,
      enhanced: null,
      requested: false,
    })
  }

  render() {
    return (
      // <div className="App">
      <Container className="App">
        <Stepper
          image={this.state.image}
          setImage={this.setImage}
          setCropped={this.setCropped}
          cropped={this.state.cropped}
          enhance={this.enhance}
          enhanced={this.state.enhanced}
          resetState={this.resetState}
          requested={this.state.requested}
        />
      </Container>
    )
  }
}

export default App
